// Articles index styling

#recent_articles, #articles {
  .container {
    max-width: 640px; } }

.article-title {
  text-decoration: none;
  h2 {
    font-size: 1.75rem; } }

.articles-pagination-wrapper {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  margin: 0 auto; }

