// Footer Styling

footer {
  margin: 3rem auto 2rem auto; }

.footer-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  ul {
    margin: 0;
    padding: 0;
    list-style: none; } }

.copyright {
  color: $highlight-color;
  margin-top: 1rem; }

.social-media-wrapper {
  display: flex;
  flex-direction: column; }

.footer-contact-wrapper {
  display: flex;
  flex-direction: column; }


